import { Injectable } from '@angular/core';
import { URL_SERVER } from '../../../environments/environment';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { map, concatAll, retryWhen, delayWhen, take } from 'rxjs/operators';
import { timer, Observable } from 'rxjs';

@Injectable({
    providedIn: 'root'
})

export class MapasService {

    constructor(private http: HttpClient) { }

    private hanflerError(error: Observable<any>) {;

            return error.pipe(
                delayWhen(() => timer(3000)),
                map((errorHttp: HttpErrorResponse) => {

                        if (errorHttp instanceof ErrorEvent) {
                            throw new Error('Error de Red');
                        } else {

                            if ( errorHttp.status === 404) {
                               throw new Error(' P치gina no encontrada');
                            } else {
                               // throw new Error(' No se pudo realizar la conexi칩n, comprueba tu red ');
                            }
                           
                        }
                }),
                take(3)
            );
    }

  

    crearSede(token: any, data: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.post(`${URL_SERVER}/mapas/sede/`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    actualizarSede(token: any, data: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.put(`${URL_SERVER}/mapas/sede/`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }


    traerSedes(token: any, data: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.put(`${URL_SERVER}/mapas/sede/traer/`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    eliminarSede(token: any, data: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.put(`${URL_SERVER}/mapas/sede/eliminar/`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    editarSede(token: any, data: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.put(`${URL_SERVER}/mapas/sede/editar/`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    buscarSedes(token: any, data: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.put(`${URL_SERVER}/mapas/sede/buscar/`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }


}
