import { Injectable } from '@angular/core';
import { URL_SERVER } from '../../../environments/environment';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { map, concatAll, retryWhen, delayWhen, take } from 'rxjs/operators';
import { timer, Observable } from 'rxjs';

@Injectable({
    providedIn: 'root'
})

export class FacturasService {

    constructor(private http: HttpClient) { }

    private hanflerError(error: Observable<any>) {;

            return error.pipe(
                delayWhen(() => timer(3000)),
                map((errorHttp: HttpErrorResponse) => {

                        if (errorHttp instanceof ErrorEvent) {
                            throw new Error('Error de Red');
                        } else {

                            if ( errorHttp.status === 404) {
                               throw new Error(' P치gina no encontrada');
                            } else {
                               // throw new Error(' No se pudo realizar la conexi칩n, comprueba tu red ');
                            }
                           
                        }
                }),
                take(3)
            );
    }


    consultarConfiguracion(token: any) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.get(`${URL_SERVER}/facturas/configuracion`, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    editarConfiguracion(token: any, data) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.post(`${URL_SERVER}/facturas/configuracion`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    guardarConfiguracion(token: any, data) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.post(`${URL_SERVER}/factura/guardar`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    consultarFacturas(token: any , data) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.post(`${URL_SERVER}/facturas/creadas`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }


    enviarrFacturas(token: any , data) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.post(`${URL_SERVER}/factura/pdf`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }

    editarFactura(token: any , data) {
        const httpHeader = {
            headers: new HttpHeaders({
                'x-token': token
            })
        };

        return this.http.post(`${URL_SERVER}/facturas/editar`, data, httpHeader).pipe(
            retryWhen(error => this.hanflerError(error))
        );
    }



}
